<template>
  <div class="personneladd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card :color="$store.state.colorSet.bgColor1">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" md="4">
                <v-text-field
                  label=""
                  prepend-icon="mdi-codepen"
                  v-model="list.account"
                  :rules="emptyRules"
                  v-if="dialog"
                  autofocus
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label=""
                  prepend-icon="mdi-key-variant"
                  v-model="list.password"
                  :rules="emptyRules"
                  reauired
                ></v-text-field>
              </v-col>

              <v-col cols="12" md="4">
                <v-text-field
                  label=""
                  prepend-icon="mdi-account"
                  v-model="list.name"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label=""
                  prepend-icon="mdi-cellphone"
                  v-model="list.cellphone"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="8">
                <v-text-field
                  label=""
                  prepend-icon="mdi-home"
                  v-model="list.address"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="1"
                  prepend-icon="mdi-account"
                  v-model="list.relationshipName1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="1"
                  prepend-icon="mdi-account-multiple"
                  v-model="list.relationship1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="1"
                  prepend-icon="mdi-phone"
                  v-model="list.relationshipPhone1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="2"
                  prepend-icon="mdi-account"
                  v-model="list.relationshipName2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="2"
                  prepend-icon="mdi-account-multiple"
                  v-model="list.relationship2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  label="2"
                  prepend-icon="mdi-phone"
                  v-model="list.relationshipPhone2"
                ></v-text-field>
              </v-col>
            </v-row>

            <v-alert outlined color="primary" text>
              <div class="d-flex">
                <header></header>
                <v-spacer></v-spacer>
                <v-btn
                  small
                  class="mx-2"
                  color="primary"
                  @click.stop="authSelectAll"
                  ></v-btn
                >
                <v-btn small color="info" @click.stop="authCancelAll"
                  ></v-btn
                >
              </div>
              <v-row>
                <v-col cols="12" md="6" v-for="authKey in authKeys" :key="authKey.keyName">
                  <v-alert :color="authKey.color" text v-if="authKey.keyName != 'exit'">
                    <v-row justify="space-around" class="px-3 align-center">
                      <v-checkbox :label="authKey.label" v-model="list[`${authKey.keyName}_key`]" value="true"></v-checkbox>
                      <v-row class="ml-10" justify="space-around" v-if="list[`${authKey.keyName}_key`]">
                        <v-checkbox label="" v-model="list[`${authKey.keyName}_add_key`]" value="true"></v-checkbox>
                        <v-checkbox label="" v-model="list[`${authKey.keyName}_edit_key`]" value="true"></v-checkbox>
                        <v-checkbox label="" v-model="list[`${authKey.keyName}_del_key`]" value="true"></v-checkbox>
                        <v-checkbox label="" v-model="list[`${authKey.keyName}_search_key`]" value="true"></v-checkbox>
                      </v-row>
                    </v-row>
                  </v-alert>
                </v-col>
                
              </v-row>
            </v-alert>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType == 'add'"
            ></v-btn
          >
          <v-btn class="success" @click="editOK" v-if="processType == 'edit'"
            ></v-btn
          >
        </v-card-actions>
        <!-- <pre>{{ list }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  props:['authKeys'],
  data() {
    return {
      dialog: false,
      list: {}, //
      oldList: {}, //
      processType: "", //addedit
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || ""],
      
    };
  },

  computed:{
    //, -> 2021/4/13 ,listauthKeys
    keyItems(){
      let keyItems = {};
      for (var authKey of this.authKeys) {
        if (authKey.keyName != "exit") {
          keyItems[`${authKey.keyName}_key`] = ""
          keyItems[`${authKey.keyName}_add_key`] = ""
          keyItems[`${authKey.keyName}_edit_key`] = ""
          keyItems[`${authKey.keyName}_del_key`] = ""
          keyItems[`${authKey.keyName}_search_key`] = ""      
        }
      }
      return keyItems;  
    }
  },

  mounted() {
    //function (showFromParent) id
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },

  methods: {
    addProcess() {
      //,
      this.processType = "add";
      this.title = "";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = { ...this.keyItems };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    editProcess(item) {
      //,
      this.processType = "edit";
      this.title = "";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //
      this.list = { ...item }; //
    },
    addOK() {
      //
      if (this.$refs.form.validate()) {
        this.list.create_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")"; //,,
        let postData = {
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        this.myAxios(
          "general/add/" + this.$store.state.databaseName + "/personnel",
          postData
        )
          .then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "" });
              this.$emit("getAllData");
            }
          })
          .catch((err) => {
            this.$swal({ icon: "err", text: "!!:" + err });
          });
      }
    },
    editOK() {
      //
      if (this.$refs.form.validate()) {
        if (this.list.edit_man == undefined) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man; //,,
        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        this.myAxios(
          "general/edit/" + this.$store.state.databaseName + "/personnel",
          postData
        ).then((rs) => {
          if (rs.state == 1) {
            this.$swal({
              icon: "success",
              text: ",",
            });
            for (var i in this.list) {
              //,vue
              this.oldList[i] = this.list[i];
            }
          }
        });
      }
    },
    authSelectAll() {
      for (var i in this.list) {
        if (i.includes("_key")) {
          this.list[i] = "true";
        }
      }
    },
    authCancelAll() {
      for (var i in this.list) {
        if (i.includes("_key")) {
          this.list[i] = "";
        }
      }
    },
  },
};
</script>

<style lang="scss">
